.CardOffers {
    text-align: center;
    margin-top: 20px;
  }
  
  .CardOffer{
    min-width: 500px;
    margin-top: 50px;
    display: flex;
    padding: 20px 40px;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    background: white;
    max-width: 800px;
    margin-bottom: 20px;
    border-radius: 10px;
  }
  
  .CardOffer .round-image {
    height: 100px;
    width: 100px;
    margin-left: 10px;
    /* margin: 0 auto; */
    border-radius: 100%;
    overflow: hidden;
    position: relative;
  }
  
  .CardOffer .item-empty {
    margin-top: 60px;
    display: flex;
    padding: 40px 40px;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    max-width: 800px;
    margin-bottom: 20px;
    border-radius: 10px;
  }
  
  .CardOffer .item-empty button {
    margin-top: 20px;
    width: 100%;
    background: #5663ff;
    color: white;
    font-size: 16px;
    padding: 12px 16px;
    border-radius: 10px;
    border: none;
  }
  
  .CardOffer .round-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
  
  .CardOffer .description {
    flex: 2;
    text-align: center;
  }
  
  .CardOffer .description h2 {
    margin: 10px 0;
  }
  .CardOffer .description h2 {
    margin: 5px 0;
    font-weight: 400;
  }
  
  .CardOffer .buttons {
    display: flex;
  }
  
  .CardItem > h3 {
    text-align: center;
  }
  