.NavMain {
  background-color: transparent;
  color: rgb(57, 57, 57);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
  height: 70px;
}

.NavMain .nav-list {
  margin-left: auto;
  display: -webkit-flex;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.background-home {
    opacity: 0.7;
    width: 100%;
}
.Button {
    margin-top: 30px;
    background: #5663ff;
    width: 8%;
    color: white;
    padding: 16px 0;
    font-size: 16px;
    border-radius: 10px;
    border: none;
    cursor: pointer;
  }
  
  .Button:active {
    background: #878cce;
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
  
.CardOffers {
    text-align: center;
    margin-top: 20px;
  }
  
  .CardOffer{
    min-width: 500px;
    margin-top: 50px;
    display: -webkit-flex;
    display: flex;
    padding: 20px 40px;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: white;
    max-width: 800px;
    margin-bottom: 20px;
    border-radius: 10px;
  }
  
  .CardOffer .round-image {
    height: 100px;
    width: 100px;
    margin-left: 10px;
    /* margin: 0 auto; */
    border-radius: 100%;
    overflow: hidden;
    position: relative;
  }
  
  .CardOffer .item-empty {
    margin-top: 60px;
    display: -webkit-flex;
    display: flex;
    padding: 40px 40px;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    max-width: 800px;
    margin-bottom: 20px;
    border-radius: 10px;
  }
  
  .CardOffer .item-empty button {
    margin-top: 20px;
    width: 100%;
    background: #5663ff;
    color: white;
    font-size: 16px;
    padding: 12px 16px;
    border-radius: 10px;
    border: none;
  }
  
  .CardOffer .round-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
  
  .CardOffer .description {
    -webkit-flex: 2 1;
            flex: 2 1;
    text-align: center;
  }
  
  .CardOffer .description h2 {
    margin: 10px 0;
  }
  .CardOffer .description h2 {
    margin: 5px 0;
    font-weight: 400;
  }
  
  .CardOffer .buttons {
    display: -webkit-flex;
    display: flex;
  }
  
  .CardItem > h3 {
    text-align: center;
  }
  
*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{-webkit-animation-duration:.01ms !important;animation-duration:.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

